.open_modal {
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 700px;
  left: auto;
  height: 30%;
  z-index: 2;
  background-color: black;
  color: white;
  border-radius: 20%;
  opacity: 0.5;
}
.modal_content {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.play_again_btn {
  width: 150px;
  font-size: 25px;
}
.play_again_btn:hover {
  background-color: grey;
}
